@font-face {
  font-family: "EuclidFlex-Medium";
  src: url("./fonts/EuclidFlex-Medium.woff2") format("woff2");
}

:root {
  --firstColor: rgb(30, 30, 30);
  --secondColor: rgb(240, 240, 240);
  --thirdColor: rgb(61, 61, 242);
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -msbox-sizing: border-box;
}


.notVisible {
  display: none;
}

html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-transform: uppercase;
  /* position: relative; */
  scroll-behavior: smooth;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 0;
}

main {
  font-family: "EuclidFlex-Medium";
  color: var(--secondColor);
  position: absolute;
  z-index: 10;
  text-transform: uppercase;
}


section {
  height: 100vh;
  width: 100vw;
  position: relative;
}

nav {
  width: 100vw;
  height: 5vh;
  padding: 2vh;
  top: 0;
  /* display: flex; */
  /* justify-content: space-between; */
}

.notVisible {
  display: none;
}

.home {
  display: block;
}

.moonPhaseContainer {
  display: block;
}

.home.img {
  height: 1vh;
}

.title {
  font-size: 6em;
  margin: 0;
  padding-left: 4vh;
  padding-top: 4vh;
  border: 0px;
}

.subtitle {
  font-size: 3.7em;
  margin: 0;
  padding-left: 4vh;
  margin-top: -1.5vh;
  border: 0px;
}

.linkName {
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  font-size: 3em;
  top: 5vh;
  margin-top: 3vh;
  margin-bottom: 3vh;
}

.buttonContainer {
  width: 100%;
  bottom: 5vh;
  display: block;
  text-align: center;
  position: absolute;
}

#headerButton {
  color: var(--secondColor);
  border: solid 1px var(--secondColor);
}

#headerButton:hover {
  background-color: var(--secondColor);
  color: var(--firstColor);
}

.button {
  text-transform: uppercase;
  font-family: "EuclidFlex-Medium";
  background-color: transparent;
  border: solid 1px var(--secondColor);
  color: var(--secondColor);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5em;
}

.button:hover {
  text-transform: uppercase;
  font-family: "EuclidFlex-Medium";
  background-color: var(--secondColor);
  color: var(--firstColor);
  font-size: 1.5em;
}

/* On screens that are 992px or less, set the background color to blue */
@media screen and (max-width: 600px) {
  @font-face {
    font-family: "EuclidFlex-Medium";
    src: url("./fonts/EuclidFlex-Medium.woff2") format("woff2"),
  }

  .title {
    font-size: 3em;
    margin: 0;
    padding-top: 1vh;
    padding-left: 1vh;
  }

  .subtitle {
    font-size: 2em;
    margin: 0;
    padding-left: 1vh;
  }

  .buttonContainer {
    bottom: 15vh;
  }
}